<style lang="scss" scoped>
.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;

  .header {
    position: relative;

    .svg-toolbar-ar {
      box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);

      svg {
        width: 60px;
        path {
          // Require for adapt animation
          fill: #9b7874;
          stroke: #9b7874;

          stroke-dasharray: 2000;
          stroke-dashoffset: 2000;
          stroke-width: 1.2em;
          stroke-miterlimit: 5;

          animation-duration: 2.5s, 1s, 1s;
          animation-delay: 0s, 1.5s, 1.5s;
        }
      }
    }
  }

  .main {
    position: relative;

    .exercise-filter-box {
      margin-top: 2em;
    }

    .tile-list {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      margin: 4em 4em;
      // For align left last row
      &::after {
        content: "";
        flex: auto;
      }
    }

    .tile-list-skeleton {
      margin: 4em 0em;
      .tile-skeleton {
        // animation extracted from https://codepen.io/jlengstorf/pen/NabRVb
        --gray-light: #ececed;
        --gray-accent: #e9e9ea;
        height: 250px;
        width: 100%;
        margin: 1em;

        &::before {
          width: 100%;
          height: 250px;
          content: "";
          display: block;
          background-image: linear-gradient(
            110deg,
            var(--gray-light) 73%,
            var(--gray-accent) 75%,
            var(--gray-light) 77%,
            var(--gray-light) 78%,
            var(--gray-accent) 84%,
            var(--gray-accent) 88%,
            var(--gray-light) 94%,
            var(--gray-light) 100%
          );
          background-size: 200% 100%;
          background-position: 0 center;
          border-radius: inherit;
          animation: 2s ease-in-out loading infinite;
        }
      }
    }
    .footer {
      width: 100%;
      background-color: whitesmoke;
      text-align: center;
      margin-top: 3em;
      padding: 0.8em;

      border-bottom-left-radius: 20px 20px;
      border-bottom-right-radius: 20px 20px;
    }
  }
}
// Animations
@keyframes loading {
  0% {
    background-position-x: 0;
  }
  40%,
  100% {
    background-position-x: -200%;
  }
}
</style>

<template>
  <div class="wrapper">
    <div class="header">
      <svg-toolbar-ar :title="$t('intranet.exercises.pageTitleExplore')" bkgColor="#5B0C20" fontColor="whitesmoke">
        <svg
          version="1.1"
          id="Capa_1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          x="0px"
          y="0px"
          viewBox="0 0 510 510"
          style="enable-background:new 0 0 510 510;"
          xml:space="preserve"
        >
          <g>
            <g id="explore">
              <path
                d="M255,226.95c-15.3,0-28.05,12.75-28.05,28.05s12.75,28.05,28.05,28.05s28.05-12.75,28.05-28.05S270.3,226.95,255,226.95z
                        M255,0C114.75,0,0,114.75,0,255s114.75,255,255,255s255-114.75,255-255S395.25,0,255,0z M311.1,311.1L102,408l96.9-209.1L408,102
                        L311.1,311.1z"
              ></path>
            </g>
          </g>
        </svg>
      </svg-toolbar-ar>
    </div>

    <div class="main">
      <exercise-filter-box @filter="searchByFilter"></exercise-filter-box>

      <div v-if="exercise.exploreList" class="tile-list md-layout md-alignment-top-center">
        <exercise-tile
          v-for="(item) in exercise.exploreList.content"
          :key="item.id"
          :exercise="item"
          class="md-layout-item md-size-30 md-medium-size-25 md-small-size-40 md-xsmall-size-100"
        />
        <!-- If visible get next page -->
        <div class="footer">
          <md-progress-spinner
            v-observe-visibility="nextPage"
            v-if="!exercise.exploreList.last"
            class="md-accent"
            :md-diameter="30"
            :md-stroke="3"
            md-mode="indeterminate"
          ></md-progress-spinner>
        </div>
      </div>
      <div v-else class="tile-list-skeleton md-layout md-alignment-top-center">
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
        <div
          class="tile-skeleton md-layout-item md-size-20 md-medium-size-25 md-small-size-40 md-xsmall-size-90"
        ></div>
      </div>
    </div>

  </div>
</template>

<script>
import { mapState } from "vuex";

import SvgToolbarAr from "@/components/svg-toolbar-ar";
import ExerciseFilterBox from "@/components/exercise-filter-box";
import ExerciseTile from "@/components/exercise-tile";

export default {
  name: "ExerciseExplore",
  components: {
    SvgToolbarAr,
    ExerciseFilterBox,
    ExerciseTile
  },
  data() {
    return {
      searchText: "",
      pagination: {
        page: 0,
        query: ""
      }
    };
  },
  computed: {
    ...mapState(["exercise"])
  },
  created: function() {
    this.$store.dispatch("exercise/EXPLORE_LIST", this.pagination);
  },
  methods: {
    searchByFilter(query) {
      this.pagination.page = 0;
      this.pagination.query = query;
      this.$store.dispatch("exercise/EXPLORE_LIST", this.pagination);
    },
    nextPage(isVisible) {
      if (isVisible) {
        this.pagination.page++;
        // For view in develop
        setTimeout(
          () => this.$store.dispatch("exercise/EXPLORE_LIST", this.pagination),
          1000
        );
      }
    }
  }
};
</script>
