<template>
  <div class="exercise-filter-box">
    <md-list>
      <md-list-item md-expand>
        <md-icon>filter_list</md-icon>
        <span class="md-list-item-text">{{ $t("components.exerciseFilter.title") }}</span>

        <md-list slot="md-expand">
          <form>
            <div class="md-layout md-gutter md-alignment-top-center">
              <!-- Name -->
              <div class="md-layout-item md-size-40 md-small-size-90">
                <md-field>
                  <label for="name">{{ $t("components.exerciseFilter.labelName") }}</label>
                  <md-input name="name" id="name" v-model="filter.name" />
                </md-field>
              </div>
              <!-- Tags -->
              <div class="md-layout-item md-size-40 md-small-size-90">
                <md-chips
                  @md-insert="removeSpaceInTag"
                  class="md-primary shake-on-error"
                  v-model="filter.tags"
                  :md-placeholder="$t('components.exerciseFilter.placeTags')"
                >
                  <div class="md-helper-text">{{ $t('components.exerciseFilter.helpTags') }}</div>
                </md-chips>
              </div>
              <!-- Hardness -->
              <div class="md-layout-item md-size-40 md-small-size-90">
                <label for="hardness">{{ $t('components.exerciseFilter.labelHardness') }}</label>
                <br />
                <md-checkbox
                  v-model="filter.hardness"
                  value="easy"
                >{{ $t('components.exerciseFilter.hardEasy') }}</md-checkbox>
                <md-checkbox
                  v-model="filter.hardness"
                  value="medium"
                >{{ $t('components.exerciseFilter.hardMedium') }}</md-checkbox>
                <md-checkbox
                  v-model="filter.hardness"
                  value="hard"
                >{{ $t('components.exerciseFilter.hardHard') }}</md-checkbox>
              </div>
              <!-- Valoration -->
              <div class="md-layout-item md-size-40 md-small-size-90">
                <label>{{ $t('components.exerciseFilter.labelValoration') }}</label>
                <br />
                <vue-slider :min="0" :max="10" :enable-cross="false" v-model="filter.valoration" />
              </div>
            </div>

            <md-button
              @click="sendFilter"
              class="md-primary"
            >{{ $t('components.exerciseFilter.button') }}</md-button>
          </form>
        </md-list>
      </md-list-item>
    </md-list>
  </div>
</template>

<script>
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";

export default {
  name: "exercise-filter-box",
  components: {
    VueSlider
  },
  data() {
    return {
      filter: {
        name: "",
        valoration: [0, 10],
        hardness: [],
        tags: []
      }
    };
  },
  methods: {
    removeSpaceInTag() {
      this.filter.tags[this.filter.tags.length - 1] = this.filter.tags[
        this.filter.tags.length - 1
      ].replace(/\s+/g, "");
    },
    sendFilter() {
      this.$emit("filter", this.generateExerciseQuery());
    },
    /*
    Expected data format
        {
            name: string,
            valoration: [Number, Number]
            hardness: Array<String>,
            tags: Array<String>
        }
    */
    generateExerciseQuery: function() {
      let query = "";
      if (this.filter.name != "") query += `name==*${this.filter.name}*`;
      // Eval satisfaction
      if (this.filter.valoration.start != "") {
        if (query != "") query += ";";
        query += `valoration>=${this.filter.valoration[0]}`;
      }
      if (this.filter.valoration.end != "") {
        if (query != "") query += ";";
        query += `valoration<=${this.filter.valoration[1]}`;
      }
      // Eval difficult
      if (this.filter.hardness.length > 0) {
        this.filter.hardness.map((value, index) => {
          switch (value) {
            case "hard":
              if (query != "" && index == 0) query += ";(";
              if (query != "" && index != 0) query += ",";
              query += `hardness>=4`;
              break;
            case "medium":
              if (query != "" && index == 0) query += ";(";
              if (query != "" && index != 0) query += ",";
              query += `hardness==3`;
              break;
            case "easy":
              if (query != "" && index == 0) query += ";(";
              if (query != "" && index != 0) query += ",";
              query += `hardness<3`;
              break;
          }
          if (index == this.filter.hardness.length-1) query += ")"
        });
      }
      // Eval tags
      if (this.filter.tags.length > 0) {
        this.filter.tags.map(value => {
          if (query != "") query += ";";
          query += `tags==*${value}*`;
        });
      }
      return query;
    }
  }
};
</script>


<style lang="scss" scoped>
.exercise-filter-box {
  .md-list {
    background-color: white;

    .valoration .md-input:first {
      margin-right: 1em;
    }
  }
  .md-button {
    float: right;
    margin-right: 5%;
  }
  .vue-slider {
    margin: 22px 0;
  }
}
.shake-on-error /deep/ .md-duplicated {
  animation-name: shake;
  animation-duration: 0.5s;
}

@keyframes shake {
  0% {
    transform: translate(15px);
  }
  20% {
    transform: translate(-15px);
  }
  40% {
    transform: translate(7px);
  }
  60% {
    transform: translate(-7px);
  }
  80% {
    transform: translate(3px);
  }
  100% {
    transform: translate(0px);
  }
}
</style>
